import { customRef } from 'vue';

export function DebouncedRef(value, delay = 4) {
    let timeout;
    return customRef((track, trigger) => {
        return {
            get() {
                track();
                return value;
            },
            set(newValue) {
                clearTimeout(timeout);
                timeout = setTimeout(() => {
                    value = newValue;
                    trigger();
                }, delay);
            }
        };
    });
}

export function range(start, end, scale = 1) {
    return Array.from(new Array(end - start)
        .keys())
        .map(p => (p + start) * scale)
}
