<template>
    <input v-model="handle">
    <button @click="update">Click</button>

    <input type="range"
           v-model="cur"
           min="0"
           :max="sub.length"
           :disabled="!sub.length"
    >

    <button @click="cur++" :disabled="cur >= sub.length"> +</button>
    <button @click="cur--" :disabled="!cur"> -</button>
    <button @click="autoInc(10)">auto</button>
    <p>{{ cur }} / {{ sub.length }} </p>

    <Heatmap class="heatmap-container" color="#0c1e55" :data="data" :xLabel="RATINGS" :yLabel="TAGS"/>


    <div>
    <button @click="doPredict()">Recommend</button>
    <ul>
        <li v-for="rec in recommend ">
            {{ RATINGS[rec.rating] }} - {{ TAGS[rec.tag] }} - {{ rec.value }}
        </li>
    </ul>
    </div>
</template>

<script src='./App.js'></script>

<style scoped>
    .heatmap-container {
        height: 80vh;
        margin: 0 auto;
    }
</style>
