import { computed, reactive, ref } from "vue";


export default {
    props: {
        data: {
            type: Array,
            default: []
        },
        xLabel: {
            type: Array,
            default: []
        },
        yLabel: {
            type: Array,
            default: []
        },
        color: {
            type: String,
            default: 'black'
        }
    },
    setup(props) {
        const xLabel = LabelConfig(props.xLabel)
        const yLabel = LabelConfig(props.yLabel)
        const data = computed(() => stdData(props.data))

        const view = {
            x: computed(() => props.data.length),
            y: computed(() => props.data.length ? props.data[0].length : 0)
        }

        return {
            view,
            data,
            xLabel,
            yLabel
        }
    }
}

function stdData(data) {
    const maxv = Math.max(...data.flatMap(d => d))
    return maxv ? data.map(r => r.map(c => `${ c / maxv * 100 }%`)) : data;
}

function LabelConfig(labels) {
    const display = true;
    return {
        labels: display ? labels : [],
        length: display ? Math.max(0, ...labels.map(l => l.length)) / 2 : 0
    }
}
