import { ref } from "vue";

export default {
    submissions: ref([]),

    async update(handle, count = undefined) { this.submissions.value = await _update(handle, count); }
};

const _userStatusUrl = (handle, count) => `https://codeforces.com/api/user.status?handle=${ handle }${ count ? `&count=${ count }` : '' }`;

async function _update(handle, count = undefined) {
    const submissions = await fetch(_userStatusUrl(handle, count))
        .then(response => response.json())
        .then(r => r.result)
        .catch(_ => undefined);

    if ( !submissions)
        return [];

    const solvedMap = new Map();

    submissions
        .filter(s => s.verdict === 'OK' && !!s.contestId && !!s.problem.rating)
        .map(s => { return { problem: s.problem, time: s.creationTimeSeconds }; })
        .forEach(e => solvedMap.set(`${ e.problem.contestId }${ e.problem.index }`, e));

    return [...solvedMap.values()]
        .sort((a, b) => a.time < b.time)
        .reverse();
}
