import * as tf from '@tensorflow/tfjs';

let model = undefined;

export async function predict(arr) {
    const model = await ensureLoadModel();
    let src = tf.tensor(arr)
    src = tf.div(src ,tf.maximum(tf.scalar(1), src.max()))

    let res = model.predict(src.reshape([1, arr.length, arr[0].length, 1]));
    return await tf.sub(res.squeeze(), src).array()
}


async function ensureLoadModel() {
    if(model == undefined)
        model = await tf.loadLayersModel('dist/assets/predict/model.json');
    return  model;
}
