import { ref, inject, computed, watchEffect } from 'vue';
import { DebouncedRef } from './core/util';
import { RATINGS, TAGS } from './core/meta';
import { predict } from "./core/model";
import Heatmap from './components/Heatmap.vue'

export default {
    components: { Heatmap },
    setup() {
        const store = inject('store');
        const sub = inject('store').submissions;

        const handle = ref('');
        const cur = DebouncedRef(0);
        const data = computed(() => heatData(sub.value.slice(0, cur.value)))

        const recommend = ref([])

        watchEffect(() => cur.value = sub.value.length)

        async function autoInc(totalTime) {
            cur.value = parseFloat(cur.value)
            if (cur.value >= sub.value.length) cur.value = 0;

            await new Promise(r => setTimeout(r, 100));

            const delayTime = Math.max(4, totalTime * 1000 / sub.value.length);

            while (cur.value + 1 < sub.value.length) {
                await new Promise(r => setTimeout(r, delayTime));
                cur.value++;
            }
        }

        async function doPredict() {
            let res = await predict(data.value)

            recommend.value = get_distribution(res)
        }

        async function update() {
            recommend.value = []
            await store.update(handle.value);
        }

        return {
            handle,
            cur,
            sub,
            data,
            recommend,
            RATINGS,
            TAGS,


            update,
            autoInc,
            doPredict
        };
    }
};

function heatData(sub) {
    let arr = new Array(RATINGS.length);

    for (let i = 0; i < RATINGS.length; ++i)
        arr[i] = new Array(TAGS.length).fill(0)

    sub.map(s => s.problem)
        .forEach(p => {
            let ridx = RATINGS.indexOf(p.rating.toString())
            p.tags.forEach(t => arr[ridx][TAGS.indexOf(t)]++)
        })

    return arr
}

function get_distribution(arr, count = 5) {
    let res = new Array()
    for (const i in RATINGS) {
        for (const j in TAGS) {
            res.push({
                rating: i,
                tag: j,
                value: arr[i][j]
            })
        }
    }
    res.sort((a, b) => b.value - a.value)
    return res.slice(0, count)
}
